.today {
  background-color: #f0f0f3e3;
  border-radius: 15px;
  margin-top: 5px;
  color: #7c7d8b;
  padding: .5em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 35em;
}

.todays-title {
  text-align: center;
  font-size: 2.8em;
  margin: 5px;
}

.expected {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.current-day {
  margin: 20px;
  text-align: center;
}

.current-temp-title {
  margin: 0;
  margin-bottom: 10px;
}

.current-temp {
  font-size: 2.8em;
  margin: 0;
  margin-right: 10px;
  display: inline-block;
}

.current-weather {
  border-right: solid;
  padding-right: 10px;
}

.todays-conditions,
.current-weather {
  text-align: center;
}

.current-location {
  font-size: 2em;
  margin: 2px;
}

.expected-conditions {
  margin: 0;
  margin-bottom: 10px;
}

.curr-high,
.curr-low {
  margin: 0;
  display: inline-block;
  font-size: 2.8em;
}

.curr-high {
  color: #cf7f04;
  margin-right: .5em;
}

.curr-low {
  color: #8AA2D0;
}

@media screen and (max-width: 1200px) {
  .todays-title {
    font-size: 2.5em;
  }
}

@media screen and (max-width: 1060px) {
  .expected {
    display: block;
  }

  .todays-conditions {
    margin-top: 2em;
  }
}


